.resetPasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('https://wallpaperaccess.com/full/4893732.jpg');
    position: fixed;
    top: 0%;
    left: 0;
  }
  
  
  .resetPasswordBox {
    background-color: transparent;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
  }
  
  
  .resetPasswordBox>div>button {
    background-color: tomato;
    height: 3px;
    width: 50%;
    border: none;
    transition: all 0.5s;
  }
  
  .resetPasswordForm {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    transition: all 0.5s;
  }
  
  /* .resetPasswordForm {
      transform: translateY(-%) translateX(-100vmax);
    } */
  
  .resetPasswordHeading {
    text-align: center;
    padding: 1.3vmax;
    color: white;
    font: 400 1.3vmax "Roboto";
    border-bottom: 1px solid rgba(242, 242, 242, 0.205);
    width: 50%;
    margin: auto;
  }
  
  .resetPasswordForm>div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  
  
  .resetPasswordForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
  }
  
  .resetPasswordForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
  }
  
  
  .resetPasswordBtn {
    background-color: tomato;
    border: none;
    padding: 0.8vmax;
    width: 100%;
    transition: all 0.5s;
    color: white;
    font: 300 0.9vmax 'Roboto';
    cursor: pointer;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  }
  
  
  .resetPasswordBtn:hover {
    background-color: rgb(179, 66, 46);
  }
  
  
  @media screen and (max-width: 600px) {
    .resetPasswordContainer {
      background-color: white;
    }
  
    .resetPasswordBox {
      width: 100vw;
      height: 95vh;
    }
  
  
    .resetPasswordForm {
      padding: 5vmax;
    }
  
  
    .resetPasswordForm>div>input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
  
    .resetPasswordForm>div>svg {
      font-size: 2.8vmax;
    }
  
    #resetPasswordImage>img {
      width: 8vmax;
      border-radius: 100%;
    }
  
  
    .resetPasswordBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }