.loginSignUpContainer {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('https://wallpaperaccess.com/full/4893732.jpg');
    background-position: center;
    background-repeat: no-repeat;
}

.loginSignUpBox {
    width: 25vw;
    height: 70vh;
    background-color: transparent;
    /* border: 2px solid rgb(61, 59, 59); */
    border-radius: 1vmax;
    box-shadow: 2px 0px 6px #ead8d866;
    overflow: hidden;
}

.login-signup-toggle {
    display: flex;
    justify-content: space-around;
}

.login-signup-toggle>p {
    font: 300 1.3vmax "Roboto";
    color: rgb(175, 166, 166);
    cursor: pointer;
}

.login-signup-toggle>p:hover {
    color: tomato;
}

button {
    height: 3px;
    border: none;
    width: 50%;
    background-color: tomato;
    transition: all 0.5s;
}

.loginForm,
.signinForm {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    transition: all 0.5s;
}


.signinForm {
    transform: translateY(-100%) translateX(-100%);
}

.loginForm>div,
.signinForm>div {
    display: flex;
    align-items: center;
}

.loginForm>div>svg,
.signinForm>div>svg {
    position: absolute;
    font-size: 2.3vmax;
    transform: translateX(1vmax);
    color: white;
}

.loginForm>div>input,
.signinForm>div>input {
    width: 100%;
    padding: 1vmax;
    padding-left: 4vmax;
    border-radius: 3vmax;
    outline: none;
    border: none;
    color: white;
    background-color: transparent;
    box-shadow: 1px 1px 3px #CFD8DC;
}

.loginForm>a {
    color: rgba(0, 0, 0, 0.651);
    text-decoration: none;
    align-self: flex-end;
    transition: all 0.5s;
    font: 500 0.8vmax 'Gill Sans';
}

.loginForm>a:hover {
    color: black;
}

.loginBtn,
.signinBtn {
    width: 90%;
    padding: 0.5vmax 0vmax;
    font: 300 1.3vmax "Roboto";
    background-color: tomato;
    border: none;
    transition: all 0.5s;
    border-radius: 1vmax;
    cursor: pointer;
}

.loginBtn:hover,
.signinBtn:hover {
    background-color: rgb(136, 86, 77);

}

#registerImg>img {
    width: 3vmax;
    border-radius: 100%;
}

#registerImg>input {
    padding: 0%;
    width: 75%;
}

#registerImg>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    z-index: 2;
    height: 5vh;
    margin: 0%;
    border: none;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 1.3vmax 0vmax;
    color: rgba(0, 0, 0, 0.623);
    background-color: transparent;
}

.shiftToRight {
    transform: translateX(100%);
}

.shiftToRightForm {
    transform: translateY(-100%) translateX(0%);
}

.shiftToNeutral {
    transform: translateX(-100%);
}

.active {
    color: tomato !important;
}

@media screen and (max-width:600px) {
    .loginSignUpBox {
        width: 100%;
        height: 100vh;
    }

    .login-signup-toggle>p {
        font: 300 2.3vmax "Roboto";
    }

    .loginForm>a {
        font: 500 1.8vmax 'Gill Sans';
    }


    .loginBtn,
    .signinBtn {

        width: 50%;
        padding: 1vmax 0vmax;
        font: 300 3vmax "Roboto";
    }

}